.timer {
  align-content: center;
  justify-content: center;
  align-items: center;
  justify-items: center;
  gap: 25px;
  font-size: 24px;
  font-weight: bold;
  display: flex;
}

.field {
  width: 50px;
  height: 100px;
  padding: none;
  margin: none;
  align-content: center;
  justify-content: center;
  align-items: center;
  justify-items: center;
}

.value {
  margin: none;
  border-radius: 5px;
  align-content: center;
  justify-content: center;
  align-items: center;
  justify-items: center;
  font-family: Times New Roman, Times, serif;
  font-size: 50px;
  position: absolute;
}

.label {
  margin: none;
  padding: none;
  align-content: center;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  position: relative;
  top: 50%;
  left: -1px;
}

.container {
  flex-wrap: wrap;
  justify-content: left;
  align-items: center;
  display: flex;
}

.container input[type="text"], .container button {
  margin: 5px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #027af2;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover, :focus {
  background-color: #9aa8b8;
  outline: none;
}

.input {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

